/*APP COMPONENT RELATED CSS GOES HERE*/

@font-face {
  font-family: 'Chalk';
  src: url('../chalk-font/KGSecondChancesSolid.ttf') format('truetype');
}

body {
  background: #24502f;
}

.App {
  background: #24502f;
}

h1 {
  font-family: 'Chalk';
  width: 100%;
  height: 190px;
  margin-top: 0;
  background: url(http://www.fbcgallatin.org/wp-content/uploads/2016/10/chalkboard.jpg);
  background-position: center;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.3);
  background-blend-mode: darken;
  color: rgb(236, 217, 42);
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 4.5rem;
  letter-spacing: 3px;
  -webkit-box-shadow: 0 1px 10px 1px rgba(0, 0, 0, 0.5);
          box-shadow: 0 1px 10px 1px rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 700px) {
  h1 {
    font-size: calc(2rem + 2.5vw)
  }
}


