.searchForm {
  width: 80%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  max-width: 1000px;
}

input {
  width: 400px;
  height: 28px;
  padding-left: 8px;
  font-size: 1.2rem;
  font-family: monospace;
  outline: none;
}

input::-webkit-input-placeholder {
  font-size: 1.2rem;
  font-family: monospace;
}

input::-ms-input-placeholder {
  font-size: 1.2rem;
  font-family: monospace;
}

input::placeholder {
  font-size: 1.2rem;
  font-family: monospace;
}

button {
  width: 200px;
  font-size: 1.2rem;
  font-family: monospace;
  border: none;
  background: white;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

button:hover {
  background: rgb(236, 217, 42);
  -webkit-box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.3);
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

select {
  width: 200px;
  height: 34px;
  outline: none;
  cursor: pointer;
  font-family: monospace;
}

.hide-button {
  opacity: 0;
  -webkit-transition: .3 ease;
  -o-transition: .3 ease;
  transition: .3 ease;
  cursor: default;
}

@media screen and (max-width: 1050px) {
  .searchForm {
    width: 90%;
  }
}

@media screen and (max-width: 930px) {
  .searchForm {
    height: 150px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
  }

  button {
    height: 32px;
  }
}


