.comparison-container {
  margin: 40px;
  display: -ms-flexbox;
  display: flex;
  padding: 40px;
  background: #3a7248ca;
  border-radius: 10px;
}

.comparison-placeholder {
  border-radius: 3px;
  border: 2px solid #333;
  -webkit-box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.3);
  padding: 4px;
  width: 306px;
  margin: 0 auto;
  height: 446px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.3rem;
  font-family: 'Chalk';
  color: white;
  text-align: center;
  padding: 15px;
  background: #24502f;
}

.comparison-window {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 20%;
  height: 400px;
  background: url(http://www.fbcgallatin.org/wp-content/uploads/2016/10/chalkboard.jpg);
  background-size: cover;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.2);
  background-blend-mode: darken;
  border-radius: 3px;
  -webkit-box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.3);
  color: white;
  padding: 15px;
  margin-top: 20px;
}

.none-selected {
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.comparison-window p,
.comparison-window h3 {
  text-align: center;
  font-size: 1.3rem;
  font-family: monospace;
}

.comparison-window h3 {
  font-size: 1.5rem;
  font-family: 'Chalk';
  letter-spacing: 2px;
}

.comparison-window h2 {
  text-align: center;
  margin-left: 0;
}

@media screen and (max-width: 950px) {
  .comparison-window {
    width: 40%;    
    margin-bottom: 20px;
  }

  .comparison-container {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    width: auto;
  }
}

@media screen and (max-width: 600px) {
  .comparison-window {
    width: 60%;
  }

  .comparison-container {
    margin: 40px auto;
    padding: 40px 0;
  }
}



